<template>
    <div class="group">      
        <input type="text" :name="name" required :autocomplete="autocomplete">
        <span class="highlight"></span>
        <span class="bar"></span>
        <label>{{ placeholder }}</label>
    </div>
</template>

<script>
    export default {
        props:  {
            placeholder: String,
            name: String,
            autocomplete: {
                type: String,
                default: 'on'
            },
        },
    }
</script>

<style scoped lang="scss">
* {
    box-sizing:border-box;
}
.group { 
    position:relative; 
    width: 90%;
    margin: 25px 5%;
}
input {
    font-size:18px;
    padding:10px 10px 10px 5px;
    display:block;
    width:300px;
    border:none;
    border-bottom:1px solid white;
    background-color: rgba(255, 255, 255, 0.13);
    width: 100%;
    color: white;
}
input:focus {
    outline:none;
}
label {
    color:white; 
    font-size:18px;
    font-weight:normal;
    position:absolute;
    pointer-events:none;
    left:5px;
    top:10px;
    transition:0.2s ease all; 
    -moz-transition:0.2s ease all; 
    -webkit-transition:0.2s ease all;
}
input:focus ~ label, input:valid ~ label {
    top:-20px;
    font-size:14px;
    color:#3AAFA9;
}
.bar {
    position:relative; display:block; width:100%;
}
.bar:before, .bar:after {
    content:'';
    height:2px; 
    width:0;
    bottom:1px; 
    position:absolute;
    background:#3AAFA9; 
    transition:0.2s ease all; 
    -moz-transition:0.2s ease all; 
    -webkit-transition:0.2s ease all;
}
.bar:before {
    left:50%;
}
.bar:after {
    right:50%; 
}
input:focus ~ .bar:before, input:focus ~ .bar:after {
    width:50%;
}
.highlight {
    position:absolute;
    height:60%; 
    width:100px; 
    top:25%; 
    left:0;
    pointer-events:none;
    opacity:0.5;
}
input:focus ~ .highlight {
    -webkit-animation:inputHighlighter 0.3s ease;
    -moz-animation:inputHighlighter 0.3s ease;
    animation:inputHighlighter 0.3s ease;
}
@-webkit-keyframes inputHighlighter {
    from {
        background:#3AAFA9;
    }
    to {
        width:0; background:transparent;
    }
}
@-moz-keyframes inputHighlighter {
	from {
        background:#3AAFA9;
    }
    to {
        width:0; background:transparent;
    }
}
@keyframes inputHighlighter {
	from {
        background:#3AAFA9;
    }
    to {
        width:0; background:transparent;
    }
}
</style>


