<template>
    <div class="row">
        <div class="col-2"></div>
        <div class="col-8">
            <my-card title="PREENCHA O FORMULÁRIO PARA REALIZAR A INSCRIÇÃO">
                <form @submit.prevent="register">
                    <text-input placeholder="Nome completo" name="name"></text-input>
                    <text-input placeholder="E-mail" name="email"></text-input>
                    <masked-input placeholder="CPF" mask="cpf" name="cpf" autocomplete="off"></masked-input>
                    <my-select placeholder="Selecione sua ocupação" name="ocupation" :values="ocupations"></my-select>
                    <my-button :disabled="vagas <= 0" type="submit">INSCREVA-SE</my-button>
                </form>
            </my-card>
        </div>
    </div>
</template>

<script>
    import Button from "./components/Button.vue";
    import Card from "./components/Card.vue";
    import TextInput from "./components/TextInput.vue";
    import MaskedInput from "./components/MaskedInput.vue";
    import Select from "./components/Select.vue";
    import db from "./firebaseInit.js";
    import moment from '../../node_modules/moment';

    export default {
        components: {
            'my-button': Button,
            'my-card': Card,
            'text-input': TextInput,
            'masked-input': MaskedInput,
            'my-select': Select
        },
        data() {
            return {
                description: 'Evento com vagas limitadas para a apresenteção do projeto PROTECA. O evento tem como finalidade orientar os participantes sobre como sem comportar caso enfrentem uma situação de aliciamento de menores.',
                ocupations: [
                    'Trabalho na Universidade Federal do Paraná',
                    'Trabalho em outro lugar, mas sou servidor público',
                    'Trabalho em outro lugar e não sou servidor público',
                    'Estudo na Universidade Federal do Paraná',
                    'Estudo em outra universidade',
                    'Atualmente não estudo nem trabalho',
                    'Nenhuma das opções'
                ],
                person: {
                    email: null,
                    name: null,
                    cpf: null,
                    ocupation: null,
                    date: null
                },
                vagas: 0
            }
        },
        created() {
            db.collection('registers').get().then(results => {
                this.vagas = 50 - results.size;
            })
        },
        methods: {
            register() {
                if (this.vagas <= 0){
                    return;
                }
                this.person.email = document.querySelector("input[name='email']").value;
                this.person.name = document.querySelector("input[name='name']").value;
                this.person.cpf = document.querySelector("input[name='cpf']").value;
                this.person.ocupation = document.querySelector("select[name='ocupation']").value;
                this.person.date = moment().format('DD/MM/YYYY, h:mm:ss');
                db.collection('registers').add({
                    name: this.person.name,
                    email: this.person.email,
                    cpf: this.person.cpf,
                    ocupation: this.person.ocupation,
                    date: this.person.date
                }).then(docRef => {
                    this.$router.push('/inscrito');
                }).catch(error => {
                    console.log(error);
                });
            },
        }
    }
</script>


