<template>
    <div class="parent">
        <div class="card">
            <div :v-if="title" class="title">{{ title }}</div>
            <slot></slot>
        </div>
    </div>
</template>

<script>
    export default {
        props: {
            title: String
        }
    }
</script>

<style scoped lang="scss">
    .parent {
        position: relative;
        height: 95vh;
    }
    .card{
        position: absolute;
        top: 50%;
        transform: translateY(-50%);
        background: #17252A;
        padding: 15px;
        box-shadow: 0 2px 2px 0 rgba(0, 0, 0, 0.14),
        0 3px 1px -2px rgba(0,0,0,0.12),
        0 1px 5px 0 rgba(0,0,0,0.2);
        .title {
            font-weight: bold;
            letter-spacing: 2px;
            text-align: center;
            line-height: 2;
            font-size: 1.2rem;
            color: white;
        }
    }
    @media only screen and (max-width: 720px){
        .card {
            position: relative;
            transform: none;
            top: 0;
            margin-top: 0;
            .title {
                letter-spacing: 1px;
                line-height: 1.5;
                font-size: 1.05rem;
            }
        }
    }
</style>


