<template>
    <div>
        <div class="row">
            <div class="col-2"></div>
            <div class="col-8">
                <my-card>
                    <div class="description-container">
                        <div class="success">INSCRIÇÃO REALIZADA!</div>
                        <div class="description">{{ description }}</div>
                    </div>
                </my-card>
            </div>
        </div>
    </div>
</template>

<script>
    import Card from "./components/Card.vue";
    export default {
        components: {
            'my-card': Card,
        },
        data() {
            return {
                description: 'Parabéns, sua inscrição foi realizada com sucesso, contamos com sua participação no evento!'
            }
        },
    }
</script>

<style scoped lang="scss">
    .success {
        font-size: 1.4rem;
        letter-spacing: 2px;
        color: #23D160;
        margin: 20px 50px 25px 50px;
        text-align: center;
        font-weight: bold;
    }
    .description {
        margin: 25px 50px 25px 50px;
        line-height: 1.8;
        text-align: center;
        color: #FFF;
    }
    .description-container {
        position: relative;
    }
    @media only screen and (max-width: 720px){
        .description {
            margin: 20px 10px 25px 10px;
            line-height: 1.5;
            text-align: center;
        }
    }
</style>


