<template>
    <div class="button-container">       
        <button @click="redirect" :type="type" :disabled="disabled">
            <slot></slot>
        </button>
    </div>
</template>

<script>
    export default {
        props: {
            type: {
                type: String,
                default: 'button'
            },
            url: {
                type: String,
                default: null
            },
            disabled: {
                type: Boolean,
                default: false
            }
        },
        methods: {
            redirect() {
                if (this.url != null){
                    this.$router.push(this.url);
                }
            }
        }
    }
</script>

<style scoped lang="scss">
    button {
        white-space: nowrap;
        text-decoration: none;
        color: #fff;
        background-color: #3AAFA9;
        text-align: center;
        letter-spacing: 2px;
        font-size: 1rem;
        font-weight: bold;
        outline: 0;
        line-height: 50px;
        padding: 0 30px;
        text-transform: uppercase;
        border: none;
        float: right;
        transition: .4s;
        box-shadow: 0 2px 2px 0 rgba(255, 255, 255, 0.14),
        0 3px 1px -2px rgba(255, 255, 255, 0.12),
        0 1px 5px 0 rgba(255, 255, 255, 0.2);
        &:hover {
            cursor: pointer;
            transform: scale(1.01);
            transition: .1s;
        }
        &:disabled {
            pointer-events:none;
            box-shadow: none;
            background-color: rgb(235, 235, 228);
            color: rgb(84, 84, 84);
        }
    }
    .button-container {
        position: relative;
        margin: 40px 0px 0px 0px;
        height: 50px;
    }
    @media only screen and (max-width: 720px){
        button {
            width: 100%;
            letter-spacing: 1px;
            font-weight: 500;
            padding: 0 10px;
        }
    }
</style>


