import Vue from 'vue'
import App from './App'
import router from './router'
require('./assets/css/grid.css');
require('./assets/css/global.css');

Vue.config.productionTip = false

new Vue({
    el: '#app',
    router,
    components: { App },
    template: '<App/>'
})


