import Vue from 'vue'
import Router from 'vue-router'
import Home from '@/components/Home'
import Form from '@/components/Form'
import Registered from '@/components/Registered'

Vue.use(Router)

export default new Router({
    mode: 'history',
    routes: [
        {
            path: '/',
            name: 'home',
            component: Home
        },
        {
            path: '/inscricao',
            name: 'form',
            component: Form
        },
        {
            path: '/inscrito',
            name: 'registered',
            component: Registered
        }
    ]
})



