<template>
    <div>
        <div class="row">
            <div class="col-2"></div>
            <div class="col-8">
                <my-card title="PROTEÇÃO DA CRIANÇA E DO ADOLESCENTE NA ERA DIGITAL">
                    <div class="description-container">
                        <div class="description">{{ description }}</div>
                    </div>
                    <span class="info">Vagas restantes: {{ vagas }}</span>
                    <div class="meta">
                        <div class="meta-title">LOCAL DO EVENTO</div>
                        <div class="meta-description">Universidade Federal do Paraná, Campus Politécnico, em frente ao R.U.</div>
                    </div>
                    <div class="meta">
                        <div class="meta-title">DATA E HORÁRIO</div>
                        <div class="meta-description">O evento será realizado no dia 22/10/2018 e começará às 14:00 com encerramento às 18:00</div>
                    </div>
                    <my-button :disabled="vagas <= 0" url="inscricao">REALIZAR INSCRIÇÃO</my-button>
                </my-card>
            </div>
        </div>
    </div>
</template>

<script>
    import Button from "./components/Button.vue";
    import Card from "./components/Card.vue";
    import db from "./firebaseInit";
    export default {
        components: {
            'my-button': Button,
            'my-card': Card,
        },
        data() {
            return {
                vagas: 0,
                description: 'Evento com vagas limitadas para a apresenteção do projeto PROTECA. O evento tem como finalidade orientar os participantes sobre como sem comportar caso enfrentem uma situação de aliciamento de menores.'
            }
        },
        created() {
            db.collection('registers').get().then(results => {
                this.vagas = 50 - results.size;
            })
            if (this.vagas < 0){
                this.vagas = 0;
            }
        }
    }
</script>

<style scoped lang="scss">
    .meta {
        margin: 5px 50px;
        color: #FFF;
        .meta-description {
            font-size: .8rem;
        }
        .meta-title {
            font-weight: bold;
            letter-spacing: 2px;
            margin-top: 20px;
            font-size: .95rem;
        }
    }
    span.info {
        display: block;
        text-align: center;
        color: #FFF;
        margin: 5px 0 35px 0px;
        font-size: 0.9rem;
        font-style: italic;
    }
    .description {
        margin: 30px 50px 0px 50px;
        line-height: 1.8;
        text-align: center;
        color: #FFF;
        &::after {
            content: "";
            position: absolute;
            width: 80%;
            height: 1px;
            bottom: 0;
            left: 10%;
            border-bottom: 1px solid #3AAFA9;
        }
    }
    .description-container {
        position: relative;
    }
    @media only screen and (max-width: 720px){
        .meta {
            margin: 5px 10px;
        }
        .description {
            margin: 20px 10px 25px 10px;
            line-height: 1.5;
            text-align: center;
        }
    }
</style>


