<template>
    <div class="select">
        <select :name="name" required>
            <option value="" selected disabled>{{ placeholder }}</option>
            <option v-for="value in values" :key="value">{{ value }}</option>
        </select>
    </div>
</template>

<script>
    export default {
        props: {
            placeholder: String,
            name: String,
            values: {
                type: Array,
                required: true
            }
        }
    }
</script>

<style scoped lang="scss">
select {
    outline: 0;
    box-shadow: none;
    border: 0 !important;
    background: #354146;
    background-image: none;
    width: 100%;
    height: 100%;
    margin: 0;
    padding: 10px 0 10px .2em;
    color: #fff;
    cursor: pointer;
    font-size: 18px;
}
.select {
    position:relative; 
    width: 90%;
    margin: 25px 5%;
    display: block;
    line-height: 43px;
    background: #354146;
    overflow: hidden;
    border-bottom:1px solid white;
    transition: .25s all ease;
}
select::-ms-expand {
  display: none;
}
.select::after {
    content: '\25BC';
    position: absolute;
    top: 0;
    right: 0;
    bottom: 0;
    padding: 0 1em;
    background: #606c72;
    pointer-events: none;
    color: #17252A;
}
.select:hover::after, .select:hover {
    color: white;
    background: #3AAFA9;
    border-bottom:1px solid #3AAFA9;
}
.select::after {
    -webkit-transition: .25s all ease;
    -o-transition: .25s all ease;
    transition: .25s all ease;
}
</style>


